'use strict';

module.exports = app => {
    class CustomController extends app.Controller {
        get user() {
            return this.ctx.session.user;
        }
        success(data) {
            this.ctx.body = {
                success: true,
                data
            };
            this.ctx.status = 200;
        }
        error(msg) {
            msg = msg || 'error';
            this.ctx.body = {
                success: false,
                message: msg
            };
        }
        notFound(msg) {
            msg = msg || 'not found';
            this.ctx.throw(404, msg);
        }
    }
    app.Controller = CustomController;
};
